.slider__block {
  position: relative;
  width: 65%;
  height: 100%;
  z-index: 0;
}

#viewport {
  width: 100%;
  height: 25vw;
  overflow: hidden;

  position: relative;
}

.slider {
  position: relative;
  transition: transform 0.2s ease;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: nowrap;
}

.slide {
  height: 100%;
  flex: 0 0 50%;
  max-width: 100%;


  position: absolute;
  top: 0;
  opacity: 0;
  z-index: 1;
  transition: opacity 3s;
}

.slide img {
  width: 100%;
  height: 100%;
  text-align: center;
  object-fit: contain;
  object-position: 0;
  pointer-events: none;
}

.show {
  opacity: 1;
  z-index: 2;
}


.slider__control {
  position: absolute;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  color: #fff;
  text-align: center;
  opacity: 0.5;
  height: 50px;
  transform: translateY(-50%);
}

.slider__control:hover,
.slider__control:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
  opacity: .9;
}

.slider__btn-left {
  left: 0;
}

.slider__btn-right {
  right: 0;
}
.slider__control::before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  background: transparent no-repeat center center;
  background-size: 100% 100%;
}

.slider__btn-left::before {
  background-image: url("../img/left-arrow.svg");
  width: 35px;
  height: 35px;
}

.slider__btn-right::before {
  background-image: url("../img/right-arrow.svg");
  width: 35px;
  height: 35px;
}


.scroll__btn {
  margin-top: auto;
  margin-bottom: 15px;
  width: 20px;
}
