body {
  font-family: $font-basic;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  color: $title-dark;

  margin: 0;
  padding: 0;
}

html,
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #141517;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  @include reset;
}

h2 {
  font-size: 2.5vw;

  &::before {
    content: " ";
    display: inline-block;
    background-color: black;
    height: 1px;
    width: 110px;
    vertical-align: middle;
    margin-top: .68em;
    margin-bottom: .7em;
    flex-grow: 0;
    flex-shrink: 0;
    margin-right: 30px;
  }

  &::after {
    content: " ";
    display: inline-block;
    background-color: black;
    height: 1px;
    width: 110px;
    vertical-align: middle;
    margin-top: .68em;
    margin-bottom: .7em;
    flex-grow: 0;
    flex-shrink: 0;
    margin-left: 30px;
  }
}
