.about__block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  & h2 {
    font-size: 2vw;
    line-height: 40px;
    text-align: center;
    color: $text-dark;
    margin-bottom: 5px;
    text-transform: uppercase;
  }

  & p {
    font-size: 1.5vw;
    line-height: 1.89vw;

    margin-bottom: 5.5px;
  }
}

.about__photo {
  width: 80vh;
  height: auto;

  & .about__img {
    position: relative;
    display: inline-block;
  }

  & .about__img::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 5px 5px rgb(216, 212, 212) inset;
  }

  & figcaption {
    font-size: 25px;
    text-align: center;
    margin-top: 20px;
  }
}
