.gallery {
  padding-top: 10%;
}

.gallery__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  padding: 0 20px;
  margin: 0;
  margin-bottom: 25px;

  width: 100%;

  & li:nth-child(4n) {
    margin-right: 0;
  }

  & .gallery__link {
    display: flex;
    flex-direction: column;
    text-align: center;
    height: 400px;
    user-select: text;
  }
}

.gallery__item {
  width: 23%;
  height: auto;
  margin-right: 2.65%;
  margin-bottom: 45px;

  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  border-radius: 3px;

  // &:hover {
  //   animation: swinging 15s ease-in-out 0s infinite;
  //   transition: transform .5s;
  //     // box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  // }

  // transform: matrix(1, 0, 0, 1, 0, -1017);
  // transition: transform 750ms ease-out 0s;
  // transform: rotateX(0deg) rotateY(0deg);

  // transform: translate3d(0,0,0) rotateX(0deg);
  // transition-timing-function: ease-out;
  // opacity: 1;
  // transform-style: preserve-3d;


  & p {
    margin: 5px 0;
  }
}

.gallery__img {
  height: 70%;
  width: 100%;

  & img {
    width: auto;
    height: 100%;
    object-fit: contain;

    cursor: zoom-in;
    border-radius: 3px;
    padding: 0 10px;

    // transition: transform 750ms ease-out 0s;
    // transform: rotateX(0deg) rotateY(0deg);

  }
}

.gallery__details {
  color: $text-dark;
  font-size: 14px;
  width: 70%;
  align-self: center;

  & td {
    padding: 0;
    margin: 0;
    text-align: start;
    width: 50%;
    border-collapse: collapse;
  }

}

// lightbox
#lightbox {
  position: fixed;
  z-index: 1000;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  display: none;
  // cursor: zoom-in;
}

#lightbox.active {
  display: flex;
  justify-content: center;
  align-items: center;
}
