.main-nav {
  width: 65%;
  max-width: 700px;

  & ul {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 4%;

    font-size: 22px;
  }

  & li {
    padding-right: 7%;
  }

  & a {
    font-size: 25px;
    color: $text-dark;
    line-height: 32px;
    font-weight: 700;

    &:hover {
      color: red;
    }

    &:focus {
      color: red;
    }

    // &:active {
    //   color: $decoration-color--05;
    // }
  }
}

@media (max-width: $tablet-width) {
  .main-nav {
      max-width: 100%;

      position: fixed;
      right: -100%;
      top: 0;
      width: 50%;
      height: 100%;
      background-color: rgb(0, 255, 242);
      overflow: auto;
      margin: 0;
      transition: right .4s;
      display: flex;
      flex-direction: column;
      padding-top: 110px;

    & ul {
      justify-content: start;

    }

    & li {
      margin-bottom: 20px;
      width: 100%;
    }

  }
}
