.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  // border: 1px solid red;
  text-align: center;
  height: 100%;
  width: 100%;

  & h2 {
    font-size: 2.5vw;
    line-height: 1.2em;
    // color: $text-dark;
    color: $text-dark;

    margin-bottom: 10px;

    &::before {
      content: none;
    }

    &::after {
      content: none;
    }
  }

  & p {
    font-size: 1em;
    line-height: 1.3em;
  }

  & .btn {
    font-size: 1em;
    color: white;

    padding: 10px 30px;
    margin-bottom: 10px;
    border-radius: 5px;
    background-color: #669999;

    box-shadow: 0 10px 10px rgba(0,0,0,0.5);
  }
}
