.contact__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  & h2 {
    font-size: 2vw;
    text-align: center;
    font-weight: 700;
    color: $text-dark;
    text-transform: uppercase;
    margin-bottom: 20px;
  }

  & p {
    font-size: 1.2vw;
  }

  & textarea {
    border-radius: 10px;

    outline: none;
    border: 1px solid #479393;
  }

  & .contact__message {
    display: flex;
    flex-direction: column;
    padding-left: 10px;
  }

  & .contact__wrapper {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    font-size: 22px;
    line-height: 25px;
    margin-bottom: 20px;
  }

  & .contact__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  & .contact__info {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 10px;
    padding: 10px;
  }

  & .contact__name,
  .contact__email {
    width: 45%;
    display: flex;
    justify-content: space-between;
  }

  & input {
    padding: 10px;
    width: 70%;

    border-radius: 5px;
    font-weight: 500;

    outline: none;
    border: 1px solid #479393;
  }

  & .contact__checkbox {
    display: flex;
  }

  & button {
    padding: 10px 30px;
    border-radius: 5px;
    background-color: rgb(249, 223, 203);

    font-family: $font-basic;
    font-size: 20px;
    line-height: 24px;
    color: white;
    background-color: #669999;

    box-shadow: 0 10px 10px rgba(0,0,0,0.5);
    outline: none;
    border: none;
    cursor: pointer;
  }
}

.contact__message {
  & label {
    margin-bottom: 25px;
  }
}

.contact__checkbox {
  & input {
    width: 20px;
    margin-right: 15px;
  }
}
